<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="商品名">
              <el-input v-model="searchs.keyword_search_value" placeholder="请输入商品名称"></el-input>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="分类">
              <el-select v-model="searchs.cats" clearable placeholder="请选择">
                <el-option v-for="{ id, name } in config.cats" :key="id" :label="name" :value="id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
              <el-button type="success" @click="create">创建</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="title" label="商品名称" align="center" />
      <el-table-column prop="cat_name" label="商品分类" align="center" />
      <el-table-column prop="max_shop_num" label="每日限购数量" align="center" />
      <el-table-column prop="status_name" label="是否下架" align="center" />
      <el-table-column prop="credit" label="积分" align="center" />
      <el-table-column prop="sort" label="排序" align="center" />
      <el-table-column prop="address" align="center" label="操作" width="200">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="eidt(scope.row.id)">编辑</el-button>
          <el-button type="danger" size="small" @click="remove(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fecth from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      searchs: {
        keyword_search_value: '',
        status: '',
        cats: '',
      },
      tableData: [],
      count: 0,
      page_curren: 1,
      config: {
        cats: null,
        status: null,
      },
      id: 0,
    }
  },
  mounted() {
    this.FetchType()
    this.FetchList()
  },
  methods: {
    showDialog() {
      this.is_show_dialog = true
    },
    remove(id) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true

            this.loading = true
            Fecth('/goods/goods/remove', {
              id: id,
            })
              .then(() => {
                done()
                this.loading = false
                instance.confirmButtonLoading = false
                this.FetchList()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                this.loading = false
              })
          } else {
            done()
          }
        },
      })
    },
    eidt(id) {
      this.$router.push({ path: '/integral/goodsCreate/', query: { id } })
    },

    FetchList() {
      this.loading = true
      Fecth('/goods/goods/search', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    FetchType() {
      this.loading = true
      Fecth('/goods/cat/search')
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.config = r.data.config
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    create() {
      this.$router.push('/integral/goodsCreate')
    },
  },
}
</script>
